import React, { useState } from "react";
import ControlsMap from "../../../Properties/PropertiesConfiguration";
import ImagePropertiesJson from "../../../Properties/ImagePropertiesJson";
import CollapsibleComponent from "../../../../ReusableComponents/CollapseCard/CollapseCard";
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker";
import ToggleBtn from "../../../../ReusableComponents/ToggleBtn/ToggleBtn";
import ImageUploadBtn from "../../../../ReusableComponents/ImageUploadBtn/ImageUploadBtn";
import ControlLabel from "../../../../ReusableComponents/ControlLabel/ControlLabel";
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css";
import SvgStyle1 from "../../../../Icons/SectionIcons/style1";
import SvgStyle2 from "../../../../Icons/SectionIcons/style2";
import SvgStyle3 from "../../../../Icons/SectionIcons/style3";
import SvgStyle4 from "../../../../Icons/SectionIcons/style4";
import SvgAlignment1 from "../../../../Icons/Image/alignment1";
import SvgAlignment2 from "../../../../Icons/Image/alignment2";
import SvgAlignment3 from "../../../../Icons/Image/alignment3";
import PlusOrange from "../../../../Icons/PlusOrange";
import RedCross from "../../../../Icons/RedCross";
import ActiveIcon from "../../../../ReusableComponents/ActiveIcon/ActiveIcon";
import {ReactComponent as TabColumn1} from "../../../../Icons/TabColumn1.svg";
import {ReactComponent as TabColumn2} from "../../../../Icons/TabColumn2.svg";
import {ReactComponent as TabColumn3} from "../../../../Icons/TabColumn3.svg";
import {ReactComponent as TabColumn4} from "../../../../Icons/TabColumn4.svg";
import {ReactComponent as TabColumn5} from "../../../../Icons/TabColumn5.svg";
import {ReactComponent as TabColumn6} from "../../../../Icons/TabColumn6.svg";
import SvgImport from "../../../../Icons/SectionIcons/importSection";
import SvgUpload from "../../../../Icons/SectionIcons/uploadControls";
import { isColor } from "../../../../Resources/globalFunctions";
import { EventListjson } from "../../../../ReusableComponents/Events/EventList";
import Events from "../../../../ReusableComponents/Events/Events";
import { FontWeightListJson } from "../../../Properties/FontWeightList";
import { validateCustomControlId } from "../../RequestLogic";
import { updateUserId } from "../../Logic/UserIdLogic";
import ErrorMessageComponent from "../../../../ReusableComponents/ErrorMessageComponent/ErrorMessageComponent";
import { regexValidator } from "../../../../Resources/globalFunctions";
import { useTranslation } from "react-i18next";
import ImageEditor from "../ImageCarouselProperties/ImageEditor";
import { GetStateFromData } from "../../../CustomHooks/CustomHooks";

function ImageProperties(props) {
  const translationStyles1 = window?.appformBuilder_configs?.selectedLanguage === "ar" || window?.appformBuilder_configs?.selectedLanguage === "ar-SA";
  const contexts = GetStateFromData(props.containerId);
  const {
    updateJson,
    changeSectionLayout,
    formState2,
    deleteControl,
    showAlertConfirmNotifications
  } = contexts.contexts;
  const { updatedComponentInFormState} = updateJson;

  const [errorMessageId, setErrorMessageId] = useState(false);
  const [editableImage, setEditableImage] = useState(false);
  const [aspectRatio, setAspectRatio] = useState("" || props?.selectedControl?.style?.imageStyle?.["enlarge"] || props.selectedControl.style["enlarge"])
  const globalTheme = formState2.theme.SectionStyle;
  const style = props.selectedControl.style;
  var CollapsibleItems = [];
  const { t } = useTranslation(["styles", "translation"]);

  Object.keys(ImagePropertiesJson).forEach((key, index) => {
    var collapseComponentBody = [];

    Object.keys(ImagePropertiesJson[key]).forEach((property) => {
      const fetchedProperty = property.replace(/\d+/g, "");
      //used to remove any numbers from property
      if(!(props?.selectedControl?.style?.imageStyle?.aspectRatio !== "auto" && ImagePropertiesJson[key][property]?.field?.toLowerCase() === "height")) {
        collapseComponentBody.push(
          returnHtml(fetchedProperty, ImagePropertiesJson[key][property], ControlsMap[fetchedProperty].type)
        );
      }
    });
    CollapsibleItems.push(<CollapsibleComponent id={index + ""} heading={key} body={collapseComponentBody} />);
  });

  function handleImageEditing(arg, imageFile) {
    if (
      arg == "Save" &&
      props.selectedControl &&
      props.selectedControl.style &&
      props.selectedControl.style["iconFile"]
    ) {
      if (props.selectedControl.style["originalIcon"]) {
      } else {
        props.selectedControl.style["originalIcon"] = props.selectedControl.style["iconFile"];
      }
      props.selectedControl.style["iconFile"] = imageFile;
      updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
    }
  }
  function handleClose() {
    setEditableImage(false);
  }
  function handleIcon(imageName, imageFile) {
    if (!props.selectedControl.style["imageList"]) props.selectedControl.style["imageList"] = [];

    props.selectedControl.style["imageList"].push({ imageName: imageName, imageFile: imageFile });
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
    props.applyChanges(props.formData);
  }
  const updateEventData = (eventJson) => {
    var count = Object.keys(eventJson).length;
    if (count > 0) {
      var Events = {};
      Events["Events"] = eventJson;
      props.selectedControl["event"] = Events;
    }
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  };

  function validateRegex(propertyName, propertyValue, event, inputBoxId) {
    try {
      let passValue=event?.key;
      if(propertyName==="CustomId"){
        passValue=[propertyValue.slice(0,event.target.selectionEnd)+event.key + propertyValue.slice(event.target.selectionEnd) ].join("");

      }
      if (
        event &&
        event.which !== 8 &&
        event.which !== 46 &&
        event.code !== "Tab" &&
        !regexValidator(passValue, ControlsMap[propertyName]["regexType"])
      ) {
        event.preventDefault();
        setErrorMessageId(inputBoxId);
      } else {
        setErrorMessageId(false);
      }
    } catch (e) {
      console.log("Error while validating regex" + e);
    }
  }

  function selectedOptionForLabel(propertyName, propertyValue) {
    let temp_style = props.selectedControl.style;
    temp_style[propertyName] = propertyValue;
    if(propertyValue === "Select" || propertyValue === "") {
      props.selectedControl.style[propertyName] =  "";
      props.selectedControl.style["renderTheme"] = !props.selectedControl.style?.["renderTheme"];
    } else {
      props.selectedControl.style[propertyName] = propertyValue;
    }

    if (propertyValue === "Regular Italic") {
      props.selectedControl.style[propertyName] = 300;
      props.selectedControl.style["FontStyle"] = "italic";
    } else if (propertyValue === "Semibold") {
      props.selectedControl.style[propertyName] = 600;
      props.selectedControl.style["FontStyle"] = "normal";
    } else if (propertyValue === "Bold") {
      props.selectedControl.style[propertyName] = "bold";
      props.selectedControl.style["FontStyle"] = "normal";
    } else if (propertyValue === "Regular") {
      props.selectedControl.style[propertyName] = 300;
      props.selectedControl.style["FontStyle"] = "normal";
    } else if (propertyValue === "Bold Italic") {
      props.selectedControl.style[propertyName] = "bold";
      props.selectedControl.style["FontStyle"] = "italic";
    } else if (propertyValue === "Light Italic") {
      props.selectedControl.style[propertyName] = 100;
      props.selectedControl.style["FontStyle"] = "italic";
    } else if (propertyValue === "Semibold Italic") {
      props.selectedControl.style[propertyName] = 600;
      props.selectedControl.style["FontStyle"] = "italic";
    } else if (propertyValue === "Light") {
      props.selectedControl.style[propertyName] = 100;
      props.selectedControl.style["FontStyle"] = "normal";
    } else if (propertyValue === "Select" || propertyValue === "") {
      props.selectedControl.style[propertyName] = "";
      props.selectedControl.style["FontStyle"] = "";
    }
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  }

  function handleProperty(propertyName, propertyValue, event, isCombo) {
    let shouldUpdate = true;
    try {
      if (propertyName === "scale") {
        props.selectedControl.style.imageStyle[propertyName] = propertyValue;
        if (propertyValue === "pixels") {
          props.selectedControl.style.imageStyle["widthType"] = "px";
          props.selectedControl.style.imageStyle["size"] = "";
          props.selectedControl.style["size"] = "";
        } else if (propertyValue === "percentage") {
          props.selectedControl.style.imageStyle["widthType"] = "%";
          props.selectedControl.style.imageStyle["width"] = "";
          props.selectedControl.style.imageStyle["height"] = "";
          props.selectedControl.style["width"] = "";
          props.selectedControl.style["height"] = "";
        }
      }

      if (propertyName === "CustomId") {
        if (propertyValue !== props.selectedControl.style.CustomId) {
          var element = event.target;
          var jsonToSend = {
            customControlId: propertyValue,
            controlid: props.selectedControl.id
          };
          validateCustomControlId(jsonToSend)
            .then((response) => {
              if (response && response.data && response.data.status === "0") {
                let updatedJson = updateUserId(
                  props.selectedControl, // whole control Json
                  propertyValue //newly defined id
                );
                if (updatedJson) {
                  updatedComponentInFormState(props.selectedControl.id, updatedJson);
                }
              } else if (response && response.data && response.data.status === "-1") {
                showAlertConfirmNotifications({
                  open: true,
                  showConfirm: "showNotification",
                  alertType: "error",
                  alertContentKey: "CustomControl Id already Exist"
                });

                element.value = props.selectedControl.style.CustomId || "";
              }
            })
            .catch(function (error) {
              // handle error
              element.value = props.selectedControl.style.CustomId || "";
              showAlertConfirmNotifications({
                open: true,
                showConfirm: "showNotification",
                alertType: "error",
                alertContentKey: `Server side error ${error}`
              });
            });
        }
        shouldUpdate = false;
      } else if (propertyName === "MaxLength") {
        propertyValue = parseInt(propertyValue);
        if (!Number.isNaN(propertyValue) && propertyValue > 0) {
          shouldUpdate = true;
        } else if (propertyValue === 0) {
          shouldUpdate = false;
        } else if (propertyValue === "") {
          shouldUpdate = true;
        } else if (Number.isNaN(propertyValue)) {
          propertyValue = "";
          shouldUpdate = true;
        }
      } else if (propertyName === "MinValue") {
        let minVal = parseInt(propertyValue);
        let maxVal = parseInt(props.selectedControl?.style?.MaxValue || "");
        if (minVal > maxVal) {
          // MIN_GREATER_THAN_MAX;
          showAlertConfirmNotifications({
            open: true,
            showConfirm: "showNotification",
            alertType: "error",
            alertContentKey: "MIN_GREATER_THAN_MAX"
          });

          window.setTimeout(() => {
            showAlertConfirmNotifications({
              open: false,
              showConfirm: "showNotification",
              alertType: "error",
              alertContentKey: "MIN_GREATER_THAN_MAX"
            });
          }, 1000);
          shouldUpdate = false;
        }
      } else if (propertyName === "MaxValue") {
        let minVal = parseInt(props.selectedControl?.style?.MinValue || "");
        let maxVal = parseInt(propertyValue);
        if (maxVal < minVal) {
          showAlertConfirmNotifications({
            open: true,
            showConfirm: "showNotification",
            alertType: "error",
            alertContentKey: "MAX_SMALLER_THAN_MIN"
          });

          window.setTimeout(() => {
            showAlertConfirmNotifications({
              open: false,
              showConfirm: "showNotification",
              alertType: "error",
              alertContentKey: "MAX_SMALLER_THAN_MIN"
            });
          }, 1000);
          shouldUpdate = false;
        }
      } else if(propertyName === "BorderColor" && !props?.selectedControl?.style?.BorderWidth) {
        props.selectedControl.style.BorderWidth = "1px";
      }
      if (shouldUpdate) {
        let temp_style = props.selectedControl.style;
        temp_style[propertyName] = propertyValue;

        if((propertyValue === "" || propertyValue === "Select") && isCombo) {
          props.selectedControl.style[propertyName] =  "";
          props.selectedControl.style["renderTheme"] = !props.selectedControl.style?.["renderTheme"];
        } else {
          props.selectedControl.style[propertyName] = propertyValue;
        }

        updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
      }
    } catch (error) {
      console.log(error);
    }
  }

  function handleStyleType(property, value) {
    handleProperty(property, value);
  }

  const parseNum = (num) => (isNaN(parseFloat(num)) ? 1 : parseFloat(num));

  function handleSupProperty(propertyName, propertyValue) {
    if (propertyName === "width" || propertyName === "height") {
      propertyValue = parseInt(propertyValue);
      if (propertyValue === 0 || propertyValue > 9999 || Number.isNaN(propertyValue)) {
        // alert
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "error",
          alertContentKey:
            propertyName === "width"
              ? "ENTER_CORRECT_WIDTH"
              : propertyName === "height"
              ? "ENTER_CORRECT_HEIGHT"
              : "ENTER_CORRECT_VALUE"
        });

        window.setTimeout(() => {
          showAlertConfirmNotifications({
            open: false,
            showConfirm: "showNotification",
            alertType: "error",
            alertContentKey:
              propertyName === "width"
                ? "ENTER_CORRECT_WIDTH"
                : propertyName === "height"
                ? "ENTER_CORRECT_HEIGHT"
                : "ENTER_CORRECT_VALUE"
          });
        }, 5000);
        if (Number.isNaN(propertyValue)) {
          propertyValue = "";
        } else {
          propertyValue = propertyValue + "";
        }
        return;
      }
    } else if (propertyName === "size") {
      propertyValue = parseInt(propertyValue);
      if (propertyValue > 100) {
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "error",
          alertContentKey: "Entered Value should not be greater than 100"
        });
        propertyValue = "";
        return;
      }
      if (propertyValue <= 0) {
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "error",
          alertContentKey: "Minimum value should be 1"
        });
        propertyValue = "";
        return;
      }
      if (isNaN(propertyValue)) {
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "error",
          alertContentKey: "Please enter valid value"
        });
        propertyValue = "";
        return;
      }
    }

    if (propertyName === "width" || propertyName === "height" || propertyName === "size") {
      props.selectedControl.style.imageStyle[propertyName] = propertyValue;
    }

    if (propertyName === "width" || propertyName === "size") {
      props.selectedControl.style.imageStyle["defaultWidth"] = propertyValue;
    }

    if (propertyName === "height" || propertyName === "size") {
      props.selectedControl.style.imageStyle["defaultHeight"] = propertyValue;
    }

    if (propertyName === "label") {
      props.selectedControl[propertyName] = propertyValue;
      props.selectedControl.style["Label"] = propertyValue;
    } else if (propertyName === "enlarge") {
      if(Number(propertyValue) <= 10){
      props.selectedControl.style["width"] =
        parseNum(propertyValue) * parseNum(props.selectedControl.style["defaultWidth"]);
      props.selectedControl.style.imageStyle["width"] =
        parseNum(propertyValue) * parseNum(props.selectedControl.style["defaultWidth"]);
      props.selectedControl.style["height"] =
        parseNum(propertyValue) * parseNum(props.selectedControl.style["defaultHeight"]);
      props.selectedControl.style.imageStyle["height"] =
        parseNum(propertyValue) * parseNum(props.selectedControl.style["defaultHeight"]);
      props.selectedControl.style[propertyName] = propertyValue;
      }
    } else {
      props.selectedControl.style[propertyName] = propertyValue;
    }

    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  }

  function handleSubPropertyChange(e) {
    if(/^[0-9]*$/.test(e.target.value) || e.target.value === ""){
      if(Number(e.target.value) > 10){
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "error",
          alertContentKey: `${t(`translation:Scale_Limit`)}.`
        });
        setAspectRatio(10)
      } else{
        setAspectRatio(e.target.value)
      }
    }
  }

  function addOptionsFunc() {
    const oldOptionsArr = props.selectedControl.options || [];
    const newOptionsArr = [...oldOptionsArr, { label: "", value: "" }];
    props.selectedControl.options = newOptionsArr;
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  }

  function handleOptions(e, index) {
    const { name, value } = e.target;
    const oldOptionsArr = props.selectedControl.options || [];
    const changes = { ...oldOptionsArr[index], [name]: value };
    oldOptionsArr[index] = changes;
    props.selectedControl.options = oldOptionsArr;
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  }

  function handleIcon(iconName, iconFile) {
    const temp_style = style;
    temp_style["iconName"] = iconName;
    temp_style["iconFile"] = iconFile;
    props.selectedControl.style["iconName"] = iconName;
    props.selectedControl.style["iconFile"] = iconFile;
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  }

  function handleToggle(e, field) {
    const value = e.target.value;

    props.selectedControl.style[field] = value;
    if (field === "resize") {
      props.selectedControl.style.imageStyle[field] = value;
    }
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  }
  function handleOpenCrop() {
    if (props.selectedControl.style && props.selectedControl.style["iconFile"]) {
      setEditableImage(true);
    }
  }

  function deleteOption(index) {
    const optionsArr = props.selectedControl.options;
    optionsArr.splice(index, 1);
    props.selectedControl.options = optionsArr;
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  }

  function getSelectedLayout(property, value) {
    changeSectionLayout(value, props.selectedControl);
  }

  function returnHtml(key, value, type) {
    switch (type.toLowerCase()) {
      case "toggle":
        return (
          <div className={value.field === "showIcon" ? "customToggleBtn1" : "toggleBtn"}>
            <span> {t(`translation:${ControlsMap[key].label}`)} </span>
            <ToggleBtn
              key={props.selectedControl.id}
              handleToggle={handleToggle}
              field={value.field}
              toggleState={style[value.field]}
            />
          </div>
        );
      case "header":
        return (
          <p
            className={styles.propertiesHeaderCheckbox}
            style={window?.appformBuilder_configs?.selectedLanguage === "ar" || window?.appformBuilder_configs?.selectedLanguage === "ar-SA" ? { textAlign: "right" } : null}
          >
            {t(`translation:${ControlsMap[key].label}`)}
          </p>
        );
      case "toggletype2":
        return (
          <div className={value.field === "showIcon" ? "customToggleBtn1" : "toggleBtn"}>
            <span> {t(`translation:${value.label}`)} </span>
            <ToggleBtn
              key={props.selectedControl.id}
              handleToggle={handleToggle}
              field={value.field}
              toggleState={style[value.field]}
            />
          </div>
        );
      case "combo":
        return (
          <div className="combo">
            {t(`translation:${ControlsMap[key].label}`)}

            {key !== "FontWeight" ? (
              <>
                <select
                  style={{ width: value.field === "zonelist" ? "121px" : null }}
                  value={style[value.field] || value.fallback}
                  onChange={(e) => handleProperty(value.field, e.target.value, undefined, true)}
                  key={props.selectedControl.id}
                >
                  {ControlsMap[key].children.map((item) => {
                    return (
                      <option style={item?.style} value={item.Value}>                        
                        {t(`translation:${item.Title}`)}
                      </option>
                    );
                  })}
                </select>
              </>
            ) : (
              <>
                <select
                  id={props.selectedControl.id + key}
                  className={styles.RightLabelSelectForText}
                  onChange={(e) => selectedOptionForLabel(value.field, e.target.value)}
                  key={props.selectedControl.id}
                >
                  {props.selectedControl.style &&
                  props.selectedControl.style["FontFamily"] &&
                  FontWeightListJson[props.selectedControl.style["FontFamily"]] ? (
                    <>
                      {FontWeightListJson[props.selectedControl.style["FontFamily"]].children.map(
                        (optionTagData, optionCount) => {
                          if (
                            FontWeightListJson[props.selectedControl.style["FontFamily"]].children[optionCount].Title
                          ) {
                            return (
                              <>
                                {FontWeightListJson[props.selectedControl.style["FontFamily"]].children[optionCount]
                                  .Value === props.selectedControl.style[key] ? (
                                  <option
                                    style={ControlsMap[key].children[optionCount]?.style}
                                    value={
                                      FontWeightListJson[props.selectedControl.style["FontFamily"]].children[
                                        optionCount
                                      ].Value
                                    }
                                    selected
                                  >                                    
                                    {t(`translation:${
                                      FontWeightListJson[props.selectedControl.style["FontFamily"]].children[
                                        optionCount
                                      ].Title
                                    }`)}
                                  </option>
                                ) : (
                                  <option
                                    style={ControlsMap[key].children[optionCount]?.style}
                                    value={
                                      FontWeightListJson[props.selectedControl.style["FontFamily"]].children[
                                        optionCount
                                      ].Value
                                    }
                                  >                                    
                                    {t(`translation:${
                                      FontWeightListJson[props.selectedControl.style["FontFamily"]].children[
                                        optionCount
                                      ].Title
                                    }`)}
                                  </option>
                                )}
                              </>
                            );
                          }
                        }
                      )}
                    </>
                  ) : (
                    <>
                      {FontWeightListJson["Open Sans"].children.map((optionTagData, optionCount) => {
                        if (FontWeightListJson["Open Sans"].children[optionCount].Title) {
                          return (
                            <>
                              {FontWeightListJson["Open Sans"].children[optionCount].Value ===
                              props.selectedControl.style[key] ? (
                                <option value={FontWeightListJson["Open Sans"].children[optionCount].Value} selected>                                  
                                  {t(`translation:${FontWeightListJson["Open Sans"].children[optionCount].Title}`)}
                                </option>
                              ) : (
                                <option value={FontWeightListJson["Open Sans"].children[optionCount].Value}>                                  
                                  {t(`translation:${FontWeightListJson["Open Sans"].children[optionCount].Title}`)}
                                </option>
                              )}
                            </>
                          );
                        }
                      })}
                    </>
                  )}
                </select>
              </>
            )}
          </div>
        );

      case "label":
        return (
          <div className="text" style={{ color: value.color, fontWeight: value.weight }}>
            {t(`translation:${value.label}`)}
          </div>
        );
      case "text":
        return (
          <div className={styles.parentDiv}>
            <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
            <div className={styles.RightTextBox}>
              {key !== "Label" ? (
                <input
                  type="text"
                  aria-label={"Control Value1"}
                  className={[
                    styles.RightLabelTextBox,
                    errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                  ].join(" ")}
                  defaultValue={props.selectedControl.style[key]}
                  key={props.selectedControl.id}
                  onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onBlur={(event) => handleProperty(key, event.target.value, event)}
                />
              ) : (
                <input
                  type="text"
                  aria-label={"Control Value2"}
                  className={[
                    styles.RightLabelTextBox,
                    errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                  ].join(" ")}
                  defaultValue={props.selectedControl.style["ButtonLabel"]}
                  key={props.selectedControl.id}
                  onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onBlur={(event) => handleProperty(key, event.target.value, event)}
                />
              )}
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );
      case "line":
        return <hr className="line" size={value.size} color={value.color} width={value.width} />;
      case "colorpicker":
        return (
          <>
            {style.ControlStyle &&
            style.ControlStyle.toLowerCase() === "style3" &&
            value.field === "BackColor" ? null : (
              <div className="colorpicker">
                {t(`translation:${value.label}`)}
                <ColorPicker
                  key={props.selectedControl.id}
                  color={isColor(style[value.field]) || isColor(globalTheme[value.field]) || isColor(value.fallback)}
                  handleProperty={handleProperty}
                  propertyName={value.field}
                />
              </div>
            )}
          </>
        );

      case "textbox":
        return (
          <div>
            <div className={styles.leftLabelForTextBox}>
              <div className={styles.labelForLabel}>{t(`translation:${ControlsMap[key].label}`)} </div>
              <input
                type="text"
                aria-label={"Control Value3"}
                defaultValue={props.selectedControl.style[key]}
                key={props.selectedControl.id + props.selectedControl.style[key]}
                id={props.selectedControl.id + key}
                maxLength={window?.appformBuilder_configs?.label_max_length}
                className={[
                  styles.textBoxLayoutForLable,
                  errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                ].join(" ")}
                onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onBlur={(event) => handleProperty(key, event.target.value, event)}
              />
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "textarea":
        return (
          <div className="textarea">
            <div className={styles.parentDiv3}>
              <span className={styles.leftLabel}>{t(`translation:${value.label}`)}</span>

              <textarea
                aria-label={props.selectedControl.style[key]}
                autoComplete="off"
                key={props.selectedControl.id}
                className={[
                  errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null,
                  styles.checkBoxLayoutStyletextArea
                ].join(" ")}
                defaultValue={
                  value.field === "label"
                    ? props.selectedControl[value.field]
                    : props.selectedControl.style[value.field]
                }
                onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onBlur={(e) => handleSupProperty(value.field, e.target.value)}
              />
            </div>
          </div>
        );
      case "input":
        return (
          <>
            {style.resize !== "fullWidth" &&
              ((props.selectedControl.style.scale === "pixels" &&
                (value.field === "width" || value.field === "height")) ||
                (props.selectedControl.style.scale === "percentage" && value.field === "size")) && (
                <div className={styles.parentDiv}>
                  <span className={styles.leftLabel}>{t(`translation:${value.label}`)}</span>
                  <div
                    className={styles.RightTextBox}
                  >
                    <input
                      type="text"
                      aria-label={"Control Value4"}
                      autoComplete="off"
                      defaultValue={
                        props?.selectedControl?.style?.imageStyle?.[value.field] ||
                        props.selectedControl.style[value.field]
                      }
                      key={props.selectedControl.id}
                      className={styles.RightLabelTextBox}
                      onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                      onBlur={(e) => handleSupProperty(value.field, e.target.value)}
                    />
                    <div className={translationStyles1 ? styles.RightTextboxPlaceholderArabic : styles.RightTextboxPlaceholder}>
                      {props.selectedControl.style.scale === "pixels" ? "PX" : "%"}
                    </div>
                  </div>
                </div>
              )}
          </>
        );
      case "longinput":
        return (
          <>
            {value.field === "imageUrl" && style.uploadType === "file" ? null : (
              <div className="longinput">
                {t(`translation:${value.label}`)}
                <input
                  className="longinput_input"
                  aria-label={"Control Value5"}
                  style={{ height: "24px" }}
                  autoComplete="off"
                  rows="2"
                  defaultValue={
                    value.field === "label"
                      ? props.selectedControl[value.field]
                      : props.selectedControl.style[value.field]
                  }
                  key={props.selectedControl.id}
                  onBlur={(e) => handleSupProperty(value.field, e.target.value)}
                />
              </div>
            )}
          </>
        );
      case "uploadbtn":
        return (
          <>
            {style.uploadType !== "url" && (
              <div className="uploadbtn">
                {t(`translation:${value.label}`)}
                <ImageUploadBtn
                  containerId={props.containerId}
                  handleImageUpload={handleIcon}
                  iconName={props.selectedControl.style.iconName}
                />
              </div>
            )}
          </>
        );
      case "uploadbtntype2":
        return (
          <>
            {props.selectedControl.style.showIcon && props.selectedControl.style.showIcon === "true" && (
              <div className="uploadbtn">
                <ImageUploadBtn
                  containerId={props.containerId}
                  handleImageUpload={handleIcon}
                  iconName={props.selectedControl.style.iconName}
                />
              </div>
            )}
          </>
        );
      case "inputcheckbox":
        return (
          <div className="inputcheckbox">
            <input aria-label={"Control Value6"} type="checkbox" /> <span> &nbsp; {value}</span>
          </div>
        );
      case "inputradio":
        return (
          <div className="inputradio">
            {value.label.map((item) => {
              return (
                <div>
                  <input
                    aria-label={"Control Value7"}
                    type="radio"
                    id={item.name}
                    name={value.field}
                    checked={item.value === style.ComboType}
                    value={item.value}
                    onClick={(e) => handleToggle(e, value.field)}
                    key={props.selectedControl.id}
                  />
                  <label aria-label={item.name} htmlFor={item.name}> &nbsp; {t(`translation:${item.name}`)}</label>
                </div>
              );
            })}
          </div>
        );
      case "longradio":
        return (
          <>
            {value.label.map((item) => {
              return (
                <div className="longradio" key={props.selectedControl.id + item.label}>
                  {item.value === "enlarge" ? null : (
                    <input
                      type="radio"
                      aria-label={"Control Value8"}
                      id={item.label}
                      name={value.field}
                      checked={item.value === style[value.field]}
                      value={item.value}
                      onClick={(e) => handleToggle(e, value.field)}
                      key={props.selectedControl.id + item.label}
                    />
                  )}
                  <label
                    title={t(`translation:${item.label}`)}
                    aria-label={item.label}
                    htmlFor={item.label}
                    style={{ marginBottom: "0px", ...(item.value === "enlarge" ? { cursor: "text" } : {}) }}
                  >
                    &nbsp; {t(`translation:${item.label}`)}
                  </label>
                  {style.resize === "dimension" && item.value === "dimension" && (
                    <select
                      value={style.scale || "pixels"}
                      onChange={(e) => handleProperty("scale", e.target.value)}
                      style={{ marginLeft: "auto" }}
                      key={props.selectedControl.id + item.label}
                    >
                      <option value="pixels"> {t(`translation:${"Pixels"}`)} </option>
                      <option value="percentage"> {t(`translation:${"Percentage"}`)} </option>
                    </select>
                  )}
                  {style.transform === "enlarge" && item.value === "enlarge" && (
                    <input
                      title={t(`translation:${item.label}`)}
                      maxLength={2}
                      type="text"
                      pattern="[0-9]"
                      key={props.selectedControl.id + item.label}
                      aria-label={"Control Value9"}
                      style={
                        translationStyles1
                          ? {
                              marginRight: "auto",
                              width: "121px",
                              ...(item.value === "enlarge" ? { cursor: "text" } : {})
                            }
                          : {
                              marginLeft: "auto",
                              width: "121px",
                              ...(item.value === "enlarge" ? { cursor: "text" } : {})
                            }
                      }
                      autoComplete="off"
                      value={aspectRatio}
                      onBlur={(e) => handleSupProperty(item.value, e.target.value)}
                      onChange={(e) => handleSubPropertyChange(e)}
                    />
                  )}
                </div>
              );
            })}
          </>
        );
      case "croplink":
        return (
          <div>
            <a
              className={styles.croplink}
              onClick={handleOpenCrop}
              style={
                props?.selectedControl?.style?.["iconFile"]
                  ? {
                      color: "#0072C6",
                      fontWeight: 600,
                    }
                  : {
                      color: "#aaa",
                      textDecoration: "none",
                      cursor: "not-allowed",
                      fontWeight: 600,
                    }
              }
              title={props?.selectedControl?.style?.["iconFile"] ? "" : "Please upload an image"}
            >
              {t(`translation:${value.label}`)}{" "}
            </a>
          </div>
        );
      case "styleoptions":
        return (
          <ActiveIcon
            labelStyle={{ fontSize: "10px", margin: "5px 0 12px 0" }}
            layout="3"
            defaultValue="Style1"
            spacing="3px"
            activeStyle={{
              padding: "0px",
              background: "#fff",
              margin: "0",
              color: "#0072C6"
            }}
            inActiveStyle={{
              padding: "0px",
              background: "#fff",
              margin: "0"
            }}
            passedValue={style.ControlStyle}
            property={value.field}
            key={props.selectedControl.id}
            handleStyleType={handleStyleType}
          >
            <SvgStyle1 value="Style1" label="Style1" />
            <SvgStyle2 value="Style2" label="Style2" />
            <SvgStyle3 value="Style3" label="Style3" />
            <SvgStyle4 value="Style4" label="Style4" />
          </ActiveIcon>
        );
      case "cardlabelvalue":
        return (
          <>
            {style.addOptionsMethod === "manually" &&
              props.selectedControl.options &&
              props.selectedControl.options.map((item, count) => {
                return (
                  <div className="cardlabelvalue">
                    <p>Option Label {count + 1}</p>
                    <input
                      autoComplete="off"
                      aria-label={"Control Value10"}
                      placeholder={item.label}
                      defaultValue={item.label}
                      onBlur={(e) => handleOptions(e, count)}
                      name="label"
                      key={props.selectedControl.id}
                    />
                    <span onClick={() => deleteOption(count)}>
                      {" "}
                      <RedCross />{" "}
                    </span>

                    <input
                      autoComplete="off"
                      aria-label={"Control Value11"}
                      placeholder={item.label}
                      value={item.label}
                      onChange={(e) => handleOptions(e, count)}
                      name="label"
                      key={props.selectedControl.id}
                    />
                    <span onClick={() => deleteOption(count)}>
                      {" "}
                      <RedCross />{" "}
                    </span>

                    <input
                      autoComplete="off"
                      aria-label={"Control Value12"}
                      placeholder={item.label}
                      defaultValue={item.label}
                      onBlur={(e) => handleOptions(e, count)}
                      name="label"
                      key={props.selectedControl.id}
                    />
                    <span onClick={() => deleteOption(count)}>
                      {" "}
                      <RedCross />{" "}
                    </span>
                  </div>
                );
              })}
          </>
        );
      case "widebutton":
        return (
          <>
            {style.addOptionsMethod === "manually" && (
              <div
                className="widebutton"
                style={{
                  border: `1px solid ${value.color}`,
                  color: value.color
                }}
                onClick={addOptionsFunc}
              >
                {" "}
                <PlusOrange color={value.color} /> &nbsp; {t(`translation:${value.label}`)}{" "}
              </div>
            )}
          </>
        );
      case "smalloptionselect":
        return (
          <div className="smalloptionselect">
            <p>{t(`translation:${value.label}`)}</p>
            {value.field === "imageAlignment" && (
              <ActiveIcon
                layout="3"
                defaultValue="right"
                spacing="3px"
                activeStyle={{
                  padding: "0",
                  background: "#fff",
                  margin: "0",
                  color: "#0072C6"
                }}
                inActiveStyle={{
                  padding: "0",
                  background: "#fff",
                  margin: "0"
                }}
                passedValue={style.imageAlignment}
                property={value.field}
                handleStyleType={handleStyleType}
                key={props.selectedControl.id}
              >
                <SvgAlignment3 value="left" />
                <SvgAlignment2 value="center" />
                <SvgAlignment1 value="right" />
              </ActiveIcon>
            )}
          </div>
        );
      case "optionsselect":
        return (
          <>
            {" "}
            {value.field || (value.field === null && style.uploadType !== "url") ? (
              <ActiveIcon
                defaultValue="file"
                layout={2}
                spacing="0"
                property={value.field}
                activeStyle={{
                  display: "flex",
                  alignItems: "center",
                  padding: "0",
                  fontWeight: "600",
                  borderRadius: "2px",
                  width: "11em",
                  height: "40px",
                  justifyContent: "center",
                  background: "#fff",
                  color: "#0072C6"
                }}
                inActiveStyle={
                  value.field === null
                    ? {
                        background: "#F0F0F0",
                        border: "1px solid #C4C4C4",
                        borderRadius: "2px",
                        width: "26.4em",
                        fontSize: "10px",
                        cursor: "default"
                      }
                    : {
                        display: "flex",
                        alignItems: "center",
                        fontWeight: "600",
                        padding: "0",
                        width: "11em",
                        borderRadius: "2px",
                        height: "40px",
                        background: "#fff",
                        justifyContent: "center",
                        color: "#C4C4C4",
                        border: "1px solid #C4C4C4"
                      }
                }
                passedValue={style[value.field]}
                handleStyleType={handleStyleType}
                key={props.selectedControl.id}
              >
                {value.label.map((item, index) => {
                  return (
                    <div value={item.value} key={index} style={{color: "#606060"}}>
                      {item.value !== null && item.value === "file" && (
                        <SvgImport
                          color={
                            style[value.field] === "file" ||
                            style[value.field] === undefined ||
                            style[value.field] === ""
                              ? "#0072C6"
                              : "#C4C4C4"
                          }
                        />
                      )}
                      {item.value !== null && item.value === "url" && (
                        <SvgUpload color={style[value.field] === "url" ? "#0072C6" : "#C4C4C4"} />
                      )}{" "}
                      &nbsp;&nbsp;
                      <span value={item.value}>{t(`translation:${item.label}`)}</span>
                    </div>
                  );
                })}
              </ActiveIcon>
            ) : null}
          </>
        );
      case "layout":
        return (
          <>
            <ActiveIcon
              layout="3"
              defaultValue="1"
              spacing="0"
              labelPosition="in"
              labelStyle={{ fontSize: "14px" }}
              activeStyle={{
                padding: "6px",
                margin: "0",
                background: "#fff",
                border: "1px solid #0072c6",
                width: "89px",
                color: "#0072C6"
              }}
              inActiveStyle={{
                padding: "6px",
                margin: "0",
                background: "#fff",
                border: "1px solid #c6c9cf",
                width: "89px"
              }}
              passedValue={props.selectedControl.ColumnLayout}
              property={value.field}
              handleStyleType={getSelectedLayout}
              key={props.selectedControl.id}
            >
               <TabColumn1 value="1" label="1 COLUMN" style={{ paddingRight: "9px" }} />
                <TabColumn2 value="2" label="2 COLUMNS"style={{paddingRight:"9px"}}  />
                <TabColumn3 value="3" label="3 COLUMNS"style={{paddingRight:"9px"}}  />
                <TabColumn4 value="4" label="4 COLUMNS"style={{paddingRight:"9px"}}  />
                <TabColumn5 value="6" label="6 COLUMNS"style={{paddingRight:"9px"}}  />
                <TabColumn6 value="12" label="12 COLUMNS"style={{paddingRight:"9px"}}  />
            </ActiveIcon>
          </>
        );
      case "databaseQuery":
        return <></>;
      case "eventbutton":
        return (
          <Events
            key={props.selectedControl.id + "_eventsProperty"}
            json={EventListjson}
            value={props.selectedControl.event}
            elemtype={props.selectedControl.elem_type}
            updateEventData={(eventJson) => updateEventData(eventJson)}
          ></Events>
        );
      default:
        return null;
    }
  }
  return (
    <>
      <ControlLabel
        label="Image Box"
        handleDelete={deleteControl}
        id={props.selectedControl.id}
        customId={props?.selectedControl?.style?.CustomId}
      />
      <div className="propertiesBody">
        {CollapsibleItems.map((item, index) => {
          return item;
        })}
      </div>
      {editableImage == true &&
      props.selectedControl &&
      props.selectedControl.style &&
      props.selectedControl.style["iconFile"] ? (
        <ImageEditor
          open={true}
          containerId={props.containerId}
          key={editableImage + Math.random()}
          imageDetails={{
            imageFile: props.selectedControl.style["originalIcon"]
              ? props.selectedControl.style["originalIcon"]
              : props.selectedControl.style["iconFile"]
          }}
          handleImageEditing={(arg, imgDetails) => handleImageEditing(arg, imgDetails)}
          handleClose={handleClose}
          selectedControl={props.selectedControl}
          imageStyle={props.selectedControl.style["imageStyle"]}
        />
      ) : (
        <></>
      )}
    </>
  );
}

export default ImageProperties;
